	module.exports = {"data":{"site":{"siteMetadata":{"title":"lasse.tech","siteUrl":"https://lasse.tech"}},"markdownRemark":{"excerpt":"The other day I was siting in the kitchen in my co-working space and ate my lunch with my colleague, who also happens to be an longtime…","html":"<p>The other day I was siting in the kitchen in my co-working space and ate my lunch with my colleague, who also happens to be an longtime vegan. And naturally somewhat devout, because else you simply can't bear the cost and the hassle of sticking with a vegan diet. You need strong conviction. Indeed <a href=\"https://www.fastcompany.com/3039505/the-vast-majority-of-vegetarians-and-vegans-eventually-return-to-meat\">80% of vegetarians and vegans revert back into eating meat</a>. At the lunch table my colleague were mixing multiple lemons with liters of water for him to hydrate and more important ensure he gets his daily nutrients, specially C-vitamins. Often a hassle for vegans who has do live without nutritional meats and milk products. Afterwards he pulled out his medicine cabinet where he kept his impressive selection of dietary supplements, pills, capsules and powder. A passerby noted it looked like his grand-moms pill collection, which was amusing and spot on. It seemed strange, well practically sick, that a young person would need to eat that amount of pills without going through serious medical treatment.</p>\n<p>I forgot about this causal everyday experience until I talked with a girl at a party. She just moved to Berlin and works as an au pair. She have become vegan as she noted at the very beginning of our conversation. Experienced have taught me that such statements, means that one have accept this dogma to continue the conversation in good spirits. I quietly obliged. The next few days it kept coming back to me, I felt that I have been preached to and that I need to decide upon the merits of it.  </p>\n<p>Since I am learning German at the moment I try to read as much as possible in German, so I looked up the German Wikipedia for <a href=\"https://de.wikipedia.org/wiki/Veganismus\">\"Veganismus\"</a>. And it struck me just how different the <a href=\"https://en.wikipedia.org/wiki/Veganism\">English</a> and German Wikipedias are. In the Anglospehere veganism are recommend and in the Germanosphere it is specially discouraged (perhaps in all of continental Europe). Most notably has the American Dietetic Association (ADA) outright recommends vegan diets:</p>\n<blockquote>\n<p>It is the position of the Academy of Nutrition and Dietetics that appropriately planned vegetarian, including vegan, diets are healthful, nutritionally adequate and may provide health benefits for the prevention and treatment of certain diseases. These diets are appropriate for all stages of the life cycle, including pregnancy, lactation, infancy, childhood, adolescence, older adulthood and for athletes.</p>\n</blockquote>\n<p>The ADA even recommends vegan diets for stages of life where nutritional needs are of special importance, such as pregnancy and childhood. This stands in stark contrast to the Germanosphere where the Deutschen Gesellschaft für Ernährung (DGE) states that it is very difficult or outright impossible to get sufficient supply of certain nutrients such as fatty acid or protine. And furthermore the DGE expressly does not recommend vegan diets for pregnant women, nursing mothers, infants, children and young people.</p>\n<p>The German Wikipedia gives, with multiple references, a detailed and concise breakdown on a per nutrient basis of the potential nutritional dangers. This made only made ADA's recommendations more incomprehensible, surely there must be an objective truth to be found. I wrote ADA for an explanation and received <a href=\"https://www.eatrightpro.org/-/media/eatrightpro-files/practice/position-and-practice-papers/position-papers/vegetarian-diet.pdf?la=en&#x26;hash=13D2FB5B600CF0778F6FE12B50ED9CE2645CAEF9\">this complex scientific paper</a>. After reading it, I momentarily though back at childhood memories of the food pyramid and how accessible and understandable it was. How times have changed, dietary recommendations are now cryptic and beyond reach for the large majority of the population.</p>\n<p>The paper goes through some critical nutrients vegan diets lack. It speaks of a \"well-planned\" vegan diet and offers advice for some nutrients, but it doesn't present evidence that vegan diets doesn't in technical terms leads to some sort of malnourishment. It gives complex best practices for vegan supplements on a variety of nutrients, such as n-3 Fatty Acids it states: </p>\n<blockquote>\n<p>\"The clinical relevance of reduced eicosapentaenoic acid (EPA) and docosahexaenoic acid (DHA) status among vegetarians and vegans is unknown. Long-chain n-3 fatty acids are important for the development and maintenance of the brain, retina, and cell membranes and favorably impact pregnancy outcomes and risk for cardiovascular disease (CVD) and other chronic diseases.\"</p>\n</blockquote>\n<p>This is complex advice for simple dietary recommendations. Does anyone without a doctoral degree know that the daily required dose of n-3 fatty acids are: \"The Dietary Reference Intake for ALA are 1.6 g/day and 1.1 g/day, for men and women, respectively\" and that these can be supplemented by a \"low-dose micro-algae based DHA supplements.\" One must presume this means vegans ideally must get their nutrial balance measure regularly to ensure that they get all the needed nutrients. Or ADA puts it:</p>\n<blockquote>\n<p>Provided that adequate nutrition education is given, a therapeutic vegetarian diet performs as well as omnivorous diets in terms of adherence. As with implementation of any diet, employing a variety of counseling strategies, including motivational interviewing, frequent sessions, cooking demonstrations, and incentives, can improve nutrition-related outcomes when using a vegetarian diet therapeutically.</p>\n</blockquote>\n<p>It takes profound effort to live by an ideal vegan diet, to the extend you have to live for it. It sounds almost absurd. It makes one speculate what lies beneath ADA's recommendation. To me it seems the primary reason for recommending vegan diets is its positive effects on weight-loss and other lifestyle health diseases – which may well be true. The benefits of weight-loss and minimizing the risks of lifestyle diseases outweigh the malnutrition that vegan diets are likely to cause. Extending this recommendation to pregnant and childern seems irresponsible. But given that 39.6% of the US population are obese, I read ADA's recommendation more as a testimony to the general appalling health situation in the population. It is pragmatism, not hard science. Weight-loss is the deciding factor at the expense of likely malnutrition. Perhaps the German DGE's recommendation will change if the obese raises from the current 23% of the population to US level.</p>","frontmatter":{"date":"27 June, 2018","path":"/on-veganism/","title":"On veganism","tags":["veganism","health"]}}},"pathContext":{}}

